* {
  margin: 0;
  padding: 0;
}

.card {
  height: 65vh;
  width: 25vw;
  border-radius: 10px;
  box-shadow: 1px 1px 8px #393e46;
  text-align: center;
  padding: 5px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card img {
  width: 100%;
  height: 25vh;
  border-radius: 10px;
}

.cardContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
}
.cardContainer button{
  height: 30px;
  width: 90px;
  background-color: #393e46;
  color: white;
  outline: none;
}
.cardContainer button:hover{
  cursor: pointer;
}
.cardContainer a{
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.card .information{
  font-weight: bold;
}

@media (max-width: 480px) {
  .card{
    width: 75%;
    height: 80%;
  }
  
}