.newsContainer{
  height: 82vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  background-color: #EEEEEE;
}
.newsContainer img:hover{
  cursor: pointer;
}
.newsContainer .empty, .mobileButtons{
  display: none;
}
@media (max-width: 480px) {
  .newsContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #EEEEEE;
    height: 100%;
  }
  .newsContainer .dirArrows{
    display: none;
  }
  .newsContainer .empty{
    display: block;
  }
  .mobileButtons{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 30px;
  }
  .mobileButtons Button{
    height: 30px;
    width: 120px;
    background-color: rgb(98, 98, 245);
    color: white;
    font-weight: bold;
    outline: none;
    border: 1px solid black;
    margin-bottom: 25px;
  }
  .mainNewsContainer{
    background-color: #EEEEEE;
  }
  #loader{
    height: 50px;
    width: 50px;
  }
}