*{
  margin: 0;
  padding: 0;
}
.footer{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 8vh;
  background-color: #393E46;
  font-weight: 500;
  position: absolute;
  top: 92vh;
  color: #EEEEEE;
}
@media (max-width: 480px) {
  .footer{
    position: sticky;
  }
}