@import url("https://fonts.googleapis.com/css2?family=Iceberg&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  height: 10vh;
  background-color: #393e46;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.navbar ul {
  display: flex;
  list-style: none;
  height: 100%;
  align-items: center;
  gap: 40px;
}
.navbar ul li {
  color: #eeeeee;
  font-weight: 500;
}

.navbar .listItem{
  text-decoration: none;
}

.heading {
  font-family: goldman;
  font-size: xx-large;
  background-image: linear-gradient(orange, white, green);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

@media (max-width: 480px) {
  .navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 15vh;
  }
  .navbar ul{
    gap: 5px;
    font-size: 12px;
  }
  
}